.home-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
  padding: 20px;
  background-size: cover;
  background-position: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.home-title {
  font-size: 3rem;
  font-weight: bold;
  color: #1b9e47; /* white text for visibility */
  text-shadow: 1px 1px 5px rgba(0,0,0,0.3);
  margin-bottom: 15px;
}

.home-subtitle {
  font-size: 1.3rem;
  color: #ac1d1d;
  text-shadow: 1px 1px 5px rgba(0,0,0,0.3);
  max-width: 500px;
  margin-bottom: 30px;
}

.home-btn {
  background: rgba(150, 215, 59, 0.8);
  color: #6c5ce7;
  padding: 12px 28px;
  font-size: 1.1rem;
  font-weight: bold;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.home-btn:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 5px 15px rgba(108, 92, 231, 0.3);
}
