/* Container */
.login-container {
  width: 400px;
  margin: 60px auto;
  padding: 30px;
  border-radius: 12px;
  background: #ffffff;
  box-shadow: 0px 4px 15px rgba(0,0,0,0.1);
  text-align: center;
}

/* Title */
.login-container h2 {
  margin-bottom: 20px;
  color: #333;
}

/* Form */
.login-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

/* Input */
.login-form input {
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  outline: none;
  font-size: 15px;
}

.login-form input:focus {
  border-color: #ff9800;
  box-shadow: 0px 0px 5px rgba(255,152,0,0.3);
}

/* Button */
.login-form button {
  padding: 12px;
  border: none;
  border-radius: 8px;
  background: #ff9800;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: 0.3s;
}

.login-form button:hover {
  background: #e68900;
}

/* Links */
.extra-links {
  margin-top: 15px;
  font-size: 14px;
}

.extra-links a {
  color: #ff9800;
  text-decoration: none;
}

.extra-links a:hover {
  text-decoration: underline;
}
