/* Container styling */
.signup-container {
  width: 400px;
  margin: 60px auto;
  padding: 30px;
  border-radius: 12px;
  background: #ffffff;
  box-shadow: 0px 4px 15px rgba(0,0,0,0.1);
  text-align: center;
}

/* Title */
.signup-container h2 {
  margin-bottom: 20px;
  color: #333;
}

/* Form */
.signup-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

/* Inputs */
.signup-form input {
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  outline: none;
  font-size: 15px;
}

.signup-form input:focus {
  border-color: #007bff;
  box-shadow: 0px 0px 5px rgba(0,123,255,0.3);
}

/* Button */
.signup-form button {
  padding: 12px;
  border: none;
  border-radius: 8px;
  background: #007bff;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: 0.3s;
}

.signup-form button:hover {
  background: #0056b3;
}

/* Links */
.extra-links {
  margin-top: 15px;
  font-size: 14px;
}

.extra-links a {
  color: #007bff;
  text-decoration: none;
}

.extra-links a:hover {
  text-decoration: underline;
}
